@tailwind base;
@tailwind components;
@tailwind utilities;

@keyframes snowfall {
  from {
    transform: translateY(-10vh);
  }
  to {
    transform: translateY(100vh);
  }
}

.snowflake {
  position: absolute;
  background: white;
  border-radius: 50%;
  pointer-events: none;
  animation: snowfall linear infinite;
}